body {

    font-family: 'Raleway', sans-serif;
}

/* Estilo padrão para o rótulo do campo de entrada */
.input-label-acompanhar {
    position: absolute;
    /* Define a posição absoluta do rótulo em relação ao contêiner pai */
    top: 3.5rem;
    /* Afasta o rótulo da parte superior do campo de entrada */
    left: 0.5rem;
    /* Afasta o rótulo da parte esquerda do campo de entrada */
    transition: all 0.25s;
    /* Adiciona uma transição suave de 0.25 segundos para alterações */
  }

  .container-pedidos-acompanhar {
    margin-bottom: 9rem;
}
.identificador {
    width: 108%;
    text-align: end;
    border-bottom: 1px solid #ccc;
    padding-right: 0.5rem;
    padding: 0.5rem;
}

.formulario-prof {
    width: 100%;
    background-color: var(--cor-de-fundo);
    /* padding-top: 5rem; */
    padding-bottom: 5rem;
}

.formulario-login__h2 {
    font-size: 1.5rem;
}

.formulario_titulo {
    margin-bottom: 2.5rem;
    /* Margem inferior de 3 unidades de medida */
    font-weight: 800;
    /* Peso da fonte 800 (negrito) */
    font-size: 1.5rem;
    /* Tamanho da fonte 1.5 unidades de medida */
}

.sem-pedido {
    margin: 2rem 2rem 2rem 0;
    font-weight: bold;
}


.radio-buttons-container,
.radio-buttons-container2 {
    flex-direction: row;
    margin-top: 7.5rem;
    justify-content: center;
}

.radio-buttons-container,
.radio-buttons-container2 input {
    margin: 0.5rem;
}

.boton-form-aceitar {
    border-radius: 5px;
    /* Arredonda os cantos do botão */
    padding: 1rem 2rem;
    /* Adiciona preenchimento interno ao botão */
    color: var(--branco);
    /* Define a cor do texto do botão como branco */
    background-color: var(--cor-de-destaque);
    /* Define a cor de fundo do botão com uma variável específica */
    font-weight: 500;
    /* Define a espessura da fonte do botão */
    border: none;
    /* Remove a borda padrão do botão */
    transition: all 0.2s ease;
    /* Adiciona um tempo para a transição ao passar o mouse */
    width: 100%;
}

.boton-form-recusar {
    border-radius: 5px;
    /* Arredonda os cantos do botão */
    padding: 1rem 2rem;
    /* Adiciona preenchimento interno ao botão */
    color: var(--branco);
    /* Define a cor do texto do botão como branco */
    background-color: red;
    /* Define a cor de fundo do botão com uma variável específica */
    font-weight: 500;
    /* Define a espessura da fonte do botão */
    border: none;
    /* Remove a borda padrão do botão */
    transition: all 0.2s ease;
    /* Adiciona um tempo para a transição ao passar o mouse */
    width: 100%;

}


/* Efeito de sombra quando o cursor passa sobre o botão no formulário */
.boton-form:hover {
    box-shadow: 1px 1px 8px 0px var(--cor-de-destaque);
    /* Adiciona uma sombra sutil ao botão ao passar o cursor sobre ele */
    cursor: pointer;
    /* Muda o cursor para uma mão quando está sobre o botão, indicando que é clicável */
}

.formulario-pedidos,
.formulario-pedidos2 {
    width: 100%;
    /* Largura total */
    background-color: white;
    /* Cor de fundo personalizada */
    padding-top: 1.5rem;
    /* Espaçamento superior de 5 unidades de medida */
    padding-bottom: 1.5rem;
    /* Espaçamento inferior de 5 unidades de medida */
    display: flex;
    /* Configuração de exibição flexível para os itens do contêiner */
    flex-direction: column;
    /* Organização dos itens em coluna */
    justify-content: center;
    /* Centralização vertical dos itens */
    align-items: center;
    /* Centralização horizontal dos itens */
    border-radius: 1.5rem;
}


.container-pedidos {
    position: relative;
    /* Define a posição do contêiner em relação ao elemento pai */
    display: flex;
    /* Configuração de exibição flexível para os itens do contêiner */
    flex-direction: column;
    /* Organização dos itens em coluna */
    justify-content: flex-end;
    /* Alinha os itens na extremidade inferior do contêiner */
    box-sizing: border-box;
    /* Configura a caixa para incluir bordas e preenchimento no tamanho total */
    width: 100%;

}

.formulario-pedidos .formulario-pedidos2 {
    border: 1px solid #ccc;
    padding: 20px;
    margin: 10px 0;
}

.container-pedidos {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90%;
}


.radio-buttons-container,
.radio-buttons-container2 {
    width: 100%;
}

.boton-form-aceitar,
.boton-form-recusar {
    margin-top: 1.5rem;
    padding: 10px;
    font-size: 14px;
    cursor: pointer;
}

.container-texto {
    margin-bottom: 1.5rem;
    margin-top: 2.5rem;
    ;
    /* Adicione um espaço na parte inferior do container de texto */
}

.container-texto-aluno {
    margin-bottom: 1.5rem;
    margin-top: -0.5rem;
    ;
    /* Adicione um espaço na parte inferior do container de texto */
}

.container-botao {
    text-align: right;
    width: 100%;
    margin: 2rem;
}

.local_botao {
    color: var(--cor-de-destaque);
    font-weight: 800;


}

.local_botao::after {
    content: '🡪';
    color: var(--cor-de-destaque);
    margin-left: 0.6rem;
}

/* Mídia para telas menores (por exemplo, até 768 pixels) */
@media only screen and (max-width: 768px) {
    .formulario-pedidos {
        padding: 1.5rem 0.5rem 1.5rem 0.5rem;
    }

    .formulario-pedidos2 {
        padding: 1rem;
        height: 13.5rem;
        padding-bottom: 6rem;
    }

    .formulario-pedidos-aluno {
        height: 7rem;
    }

    .container-texto-aluno {
        margin-bottom: 1.5rem;
        margin-top: -0.5rem;
        ;
        /* Adicione um espaço na parte inferior do container de texto */
    }

    .radio-buttons-container2 {
        flex-direction: row;
        margin-top: 9.5rem;
        justify-content: center;
    }

    .boton-form-aceitar,
    .boton-form-recusar {
        padding: 0.8rem;
        font-size: 12px;
    }

    .identificador {
        width: 110%;
    }
}